
.home-item {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0.05);
  display: block;
  padding: .25rem 0;
  border-bottom: 1px solid #f5f5f5;
}
.home-item > .content.hasCover > h3,
  .home-item > .content.hasCover > p {
    margin-right: 1.14rem;
}
.home-item > .content > p {
    -webkit-line-clamp: 3;
}
.home-item > .content .cover {
    display: inline-block;
    width: .88rem;
    height: .88rem;
    background-size: cover;
}
.home-item > .content > h3 {
    font-size: .17rem;
    line-height: 1.4;
}
.home-item > .content > p {
    font-size: .12rem;
    color: #aaa;
    line-height: 1.5;
    margin-top: .05rem;
}
.home-item .layout2 {
    margin-bottom: .12rem;
    border: none;
    padding: 0;
}
.home-item .layout2 .cover-2 {
      width: 2.54rem;
      height: 1.64rem;
      background-size: cover;
      display: inline-block;
}
.home-item .layout2 .title {
      margin-top: .06rem;
}
.home-item .layout2 .more-pic > * {
      width: .8rem;
      height: .8rem;
      background-size: cover;
}
.home-item .layout2 .more-pic .more-pic-1 {
      margin-bottom: .04rem;
}
.home-item .author {
    width: 100%;
    color: #ccc;
    font-size: .12rem;
    margin-top: .05rem;
}



