
#home {
  margin: 0 .18rem;
}
#quick-nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  width: 100%;
  margin-top: .26rem;
}
#quick-nav > li {
    box-sizing: border-box;
    width: 50%;
    padding: .03rem;
}
#quick-nav > li > a {
      text-align: center;
      line-height: .44rem;
      display: block;
      border-radius: .02rem;
      background: #f6f6f6;
      -webkit-tap-highlight-color: rgba(0, 0, 0, 0.05);
}
.load-more {
  margin-top: 25px;
  margin-bottom: 50px;
  height: .4rem;
  background: url("/static/img/body/home/loading_grey.gif") 50% 50% no-repeat;
  background-size: .24rem .24rem;
}
.no-more {
  text-align: center;
  line-height: 3;
}
.first-loading {
  position: fixed;
  margin: auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: inline-block;
  width: 0.4rem;
  height: 0.4rem;
  background: url("/static/img/loading_green.gif") no-repeat;
  background-size: cover;
}



