
@charset "UTF-8";
body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
legend,
input,
button,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
}
body {
  color: #494949;
  font-size: 0.14rem;
  font-family: '"Helvetica Neue",Helvetica,Roboto,Arial,sans-serif', Verdana, Arial, Helvetica, sans-serif;
  line-height: normal;
}
a {
  color: #494949;
  cursor: pointer;
  text-decoration: none;
}
li {
  list-style: none;
}
.clearfix:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
.clearfix {
  *zoom: 1;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.horizontal > * {
  display: inline-block;
}
.vertical-center {
  display: table;
}
.vertical-center > *:first-child {
    display: table-cell;
    vertical-align: middle;
}
.icon-text-hor > * {
  vertical-align: middle;
}
.mobile-1px:after {
  content: '';
  display: block;
  height: 1px;
  position: absolute;
  left: 0;
  top: 100%;
  width: 100%;
  background: #f3f3f3;
  transform: scaleY(0.5);
}
.text-overflow {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.text-overflow-multi-line {
  overflow: hidden;
  text-overflow: ellipsis;
  /* autoprefixer: off*/
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  /* autoprefixer: on*/
}
