
.movie-book-classify {
  margin-left: .18rem;
  padding-bottom: .3rem;
}
.movie-book-classify > .title {
    font-size: .16rem;
    line-height: .56rem;
    margin-top: .14rem;
}
.movie-book-classify > ul {
    overflow: hidden;
}
.movie-book-classify > ul li {
      display: inline-block;
      box-sizing: border-box;
      width: 50%;
      line-height: .42rem;
      border-top: 1px solid #eee;
      border-bottom: 1px solid #eee;
      margin-top: -1px;
}
.movie-book-classify > ul li a {
        display: block;
        color: #42bd56;
}
.movie-book-classify > ul li .right-ward-icon {
        display: inline-block;
        border-right: solid 1px #ccc;
        border-bottom: solid 1px #ccc;
        width: .1rem;
        height: .1rem;
        vertical-align: middle;
        transform: rotate(-45deg);
        margin-top: .16rem;
        margin-right: .2rem;
}
.movie-book-classify > ul li:nth-child(even) {
      border-left: 1px solid #eee;
}
.movie-book-classify > ul li:nth-child(even) a {
        margin-left: .2rem;
}



